/*====================================================
=            timeline list + badge-circle            =
====================================================*/

$tl-circle-size: 50px;
$tl-line-height: 1.5em;
$tl-border-width: 2px;

.badge-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    left: math.div(-$tl-circle-size - $tl-border-width, 2);
    top: math.div(-$tl-circle-size - $tl-border-width, 2) + 1px;
    height: $tl-circle-size;
    width: $tl-circle-size;
    min-width: $tl-circle-size;
    font-size: math.div($tl-circle-size, 3);
    border-radius: 50%;
}

.list-timeline {
    padding-left: math.div($tl-circle-size, 2);
    padding-top: math.div($tl-circle-size, 2);
    > li {
        border-left: $tl-border-width solid #ddd;
        .timeline-content {
            position: relative;
            top: math.div(-$tl-line-height, 2);
            overflow: auto;
        }
        &:last-child {
            border-left: $tl-border-width solid transparent;
        }
    }
}
