/*=============================================
=        Cards (template isotope grid)        =
=============================================*/

.grid {
    .card {
        display: block;
        position: relative;
        margin: 0 0 2rem 0;
        transition: box-shadow 0.25s;
        border-radius: 2px;
        padding: 20px;
        display: block;
        color: $gray-dark;
        min-height: 60px;

        .badge {
            position: absolute;
            right: 20px;
            font-size: 12px;
            top: 50%;
            margin-top: -9px;
        }

        &.bg-bs4 {
            background-color: $bs4;
            .badge {
                background: darken($bs4, 15%);
            }
            .item {
                color: color-contrast($bs4);
            }
        }

        &.bg-bs5 {
            background-color: $bs5;
            .badge {
                background: darken($bs5, 15%);
            }
            .item {
                color: color-contrast($bs5);
            }
        }

        &.bg-bulma {
            background-color: $bulma;
            .badge {
                background: darken($bulma, 15%);
            }
            .item {
                color: color-contrast($bulma);
            }
        }

        &.bg-foundation {
            background-color: $foundation;
            .badge {
                background: darken($foundation, 15%);
            }
            .item {
                color: color-contrast($foundation);
            }
        }

        &.bg-material-bootstrap {
            background-color: $material-bootstrap;
            .badge {
                background: darken($material-bootstrap, 15%);
            }
            .item {
                color: color-contrast($material-bootstrap);
            }
        }

        &.bg-material {
            background-color: $material;
            .badge {
                background: darken($material, 15%);
            }
            .item {
                color: color-contrast($material);
            }
        }

        &.bg-tailwind {
            background-color: $tailwind;
            .badge {
                background: darken($tailwind, 15%);
            }
            .item {
                color: color-contrast($tailwind);
            }
        }

        &.bg-uikit {
            background-color: $uikit;
            .badge {
                background: darken($uikit, 15%);
            }
            .item {
                color: color-contrast($uikit);
            }
        }

        .item {
            font-size: 1.0625rem;
        }

        &:hover {
            text-decoration: none;
        }

        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);

        &.has-icon {
            padding-left: 85px;
            padding-right: 95px;
            border: none;

            &:before {
                font-family: "icomoon";
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 1.75rem;
                color: #fff;
                border-radius: $border-radius 0 0 $border-radius;
                width: 54px;
                height: 100%;
            }

            &:after {
                content: " ";
                position: absolute;
                top: calc(50% - 6px);
                left: 54px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 6px 0 6px 6px;
            }

            &.bg-bs4 {
                &:before {
                    text-shadow: 1px 1px 3px darken($bs4, 30%);

                    background: darken($bs4, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($bs4, 15%);
                }

                &:hover {
                    background: darken($bs4, 15%);

                    &:before {
                        background: darken($bs4, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($bs4, 20%);
                    }
                }
            }

            &.bg-bs5 {
                &:before {
                    text-shadow: 1px 1px 3px darken($bs5, 30%);

                    background: darken($bs5, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($bs5, 15%);
                }

                &:hover {
                    background: darken($bs5, 15%);

                    &:before {
                        background: darken($bs5, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($bs5, 20%);
                    }
                }
            }

            &.bg-bulma {
                &:before {
                    text-shadow: 1px 1px 3px darken($bulma, 30%);

                    background: darken($bulma, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($bulma, 15%);
                }

                &:hover {
                    background: darken($bulma, 15%);

                    &:before {
                        background: darken($bulma, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($bulma, 20%);
                    }
                }
            }
            &.bg-foundation {
                &:before {
                    text-shadow: 1px 1px 3px darken($foundation, 30%);

                    background: darken($foundation, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($foundation, 15%);
                }

                &:hover {
                    background: darken($foundation, 15%);

                    &:before {
                        background: darken($foundation, 25%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($foundation, 25%);
                    }
                }
            }

            &.bg-material-bootstrap {
                &:before {
                    text-shadow: 1px 1px 3px darken($material-bootstrap, 30%);

                    background: darken($material-bootstrap, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($material-bootstrap, 15%);
                }

                &:hover {
                    background: darken($material-bootstrap, 15%);

                    &:before {
                        background: darken($material-bootstrap, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($material-bootstrap, 20%);
                    }
                }
            }

            &.bg-material {
                &:before {
                    text-shadow: 1px 1px 3px darken($material, 30%);

                    background: darken($material, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($material, 15%);
                }

                &:hover {
                    background: darken($material, 15%);

                    &:before {
                        background: darken($material, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($material, 20%);
                    }
                }
            }

            &.bg-tailwind {
                &:before {
                    text-shadow: 1px 1px 3px darken($tailwind, 30%);

                    background: darken($tailwind, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($tailwind, 15%);
                }

                &:hover {
                    background: darken($tailwind, 15%);

                    &:before {
                        background: darken($tailwind, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($tailwind, 20%);
                    }
                }
            }

            &.bg-uikit {
                &:before {
                    text-shadow: 1px 1px 3px darken($uikit, 30%);

                    background: darken($uikit, 15%);
                }

                &:after {
                    border-color: transparent transparent transparent darken($uikit, 15%);
                }

                &:hover {
                    background: darken($uikit, 15%);

                    &:before {
                        background: darken($uikit, 20%);
                    }

                    &:after {
                        border-color: transparent transparent transparent darken($uikit, 20%);
                    }
                }
            }
        }
    }
}

/*============================================
=            responsive card-deck            =
============================================*/

/* Bootstrap 4 breakpoints & gutter*/
$card-grid-breakpoints: (
    xs: 0,
    md: 768px,
    lg: 992px,
) !default;

/* $grid-gutter-width: 30px !default;*/

/* number of cards per line for each breakpoint*/
$cards-per-line: (
    xs: 1,
    md: 2,
    lg: 3,
);

@each $name, $breakpoint in $card-grid-breakpoints {
    @media (min-width: $breakpoint) {
        .bg-deck .card {
            flex: 0 0 calc(#{math.div(100, map-get($cards-per-line, $name))}% - #{$grid-gutter-width});
        }
    }
}
