/*==========================================================================
=            Filetree (https://jsfiddle.net/mehmetatas/fXzHS/2/)            =
==========================================================================*/

.tree li {
    margin: 0 0 !important;
    list-style-type: none;
    position: relative;
    padding: 20px 5px 0px 5px;
    small {
        display: inline-block;
    }
}

.tree li::before {
    content: "";
    position: absolute;
    top: 0;
    width: 1px;
    height: 100%;
    right: auto;
    left: -20px;
    border-left: 1px solid #ccc;
    bottom: 50px;
}

.tree li::after {
    content: "";
    position: absolute;
    top: 30px;
    width: 25px;
    height: 20px;
    right: auto;
    left: -20px;
    border-top: 1px solid #ccc;
}

.tree li a[href="#"] {
    display: inline-block;
    vertical-align: top;
    padding: 5px 10px;
    text-decoration: none;
    color: #666;
    font-family: arial, verdana, tahoma;
    font-size: 11px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;

    &.folder {
        background: #ffe79c;
        border: 1px solid darken(#ffe79c, 30%);
    }

    ~ ul li a.folder ~ ul li a.folder {
        background: lighten(#ffe79c, 10%);
        border: 1px solid darken(#ffe79c, 10%);
        ~ ul li a.folder {
            background: lighten(#ffe79c, 18%);
            border: 1px solid #ffe79c;
        }
    }

    &.file {
        background: lighten(desaturate($info, 20%), 40%);
        border: 1px solid darken($info, 10%);
    }

    ~ ul li a.folder ~ ul li a.folder ~ ul li a.file {
        background: lighten(desaturate($info, 20%), 55%);
        border: 1px solid lighten($info, 18%);
    }
}

/*Remove connectors before root*/
.tree > ul > li::before,
.tree > ul > li::after {
    border: 0;
}

/*Remove connectors after last child*/
.tree li:last-child::before {
    height: 30px;
}

/*Time for some hover effects*/

/*We will apply the hover effect the the lineage of the element also*/
.tree li a:hover,
.tree li a:hover + ul li a {
    &.folder {
        background: darken(#ffe79c, 10%);
        border: 1px solid darken(#ffe79c, 40%);
    }

    &.file {
        background: lighten(desaturate($info, 20%), 30%);
        border: 1px solid darken($info, 20%);
    }

    color: #000;
}

/*Connector styles on hover*/
.tree li a:hover + ul li::after,
.tree li a:hover + ul li::before,
.tree li a:hover + ul::before,
.tree li a:hover + ul ul::before {
    border-color: #94a0b4;
}
