// Settings

// number of social networks
$social-networks: 13;

// number of columns
$colums: 2;

$button-width: 2.5rem;
$button-height: 2.5rem;
$button-animation: all 200ms ease;

.pace-done #share-wrapper {
    display: block;
}

#share-wrapper {
    position: absolute;
    top: 37px;
    z-index: 1050;
    right: 50px;
    height: 40px;
    transform: translateY(-50%);
    &:hover:before {
        position: absolute;
        right: 110%;
        top: 4px;
        content: "Help us & share!";
        display: block;
        white-space: nowrap;
        padding: 0.33rem 1rem;
        background: #4080ff;
        color: #fff;
    }
    ul {
        width: calc(#{$colums} * 45px);
        position: relative;
        left: calc(-50% + 22.5px);
        li {
            width: 45px;
        }
    }
    li > a,
    label {
        color: #efefef;
        border-radius: 50%;
        text-decoration: none;
        width: $button-width;
        height: $button-height;
        overflow: hidden;
        cursor: pointer;
        &#share {
            background: #4267b2;
            position: relative;
            z-index: $social-networks + 1;
            opacity: 0.75;
            &:hover {
                background: #4080ff;
            }
        }
        &#share-facebook {
            background: #3b5998;
        }
        &#share-twitter {
            background: #00acee;
        }
        &#share-pinterest {
            background: #e4405f;
        }
        &#share-linkedin {
            background: #0077b5;
        }
        &#share-reddit {
            background: #ff4500;
        }
        &#share-google-bookmarks {
            background: #4285f4;
        }
        &#share-mix {
            background: #ff8226;
        }
        &#share-pocket {
            background: #ee4056;
        }
        &#share-digg {
            background: #2a2a2a;
        }
        &#share-blogger {
            background: #fda352;
        }
        &#share-tumblr {
            background: #35465c;
        }
        &#share-flipboard {
            background: #cc0000;
        }
        &#share-hacker-news {
            background: #ff6600;
        }
    }
    li {
        $i: 1;
        @for $sn from $i through $social-networks {
            $delay: math.div($sn * 100, 6);
            &:nth-child(#{$sn}) {
                transition-delay: #{$delay}ms;
                opacity: 0;
                transform: translateY(-30%);
                z-index: $social-networks + $i - $sn;
            }
        }
        transition: $button-animation;
    }
    input {
        ~ ul {
            visibility: hidden;
        }
        &:checked {
            ~ label,
            ~ ul {
                visibility: visible;
                li {
                    transform: scale(1) translateY(0);
                    opacity: 1;
                }
            }
        }
    }
}

@media only screen and (min-width: 992px) {
    #share-wrapper {
        position: fixed;
        top: 180px;
        right: 0;
        label#share {
            right: -42px;
        }
        #share-wrapper ul {
            right: 2px;
        }
    }
}

.fb-like {
    top: 90px;
    left: 80px;
    width: 42px;
    position: absolute !important;
    z-index: 1050;
}

@media only screen and (min-width: 992px) {
    .fb-like {
        position: fixed !important;
        top: 170px;
        right: 38px;
        left: auto;
    }
}
