/*===============================
=            Sidebar            =
===============================*/

$navbar-height: 72px;
$navbar-left-width: 14.375rem; // 230px
$navbar-left-color: #fff;
$navbar-left-background: $gray-800;
$navbar-left-active-background: $red-600;

#navbar-left-wrapper {
    display: none;
    position: fixed;
    z-index: 9999;
    top: $navbar-height;
    padding-right: 0;
    width: $navbar-left-width;
    height: 100%;
    background-color: $navbar-left-background;
    z-index: 2;
    box-shadow: 2px 0px 1px rgba(0, 0, 0, 0.12), 1px 0px 1px rgba(0, 0, 0, 0.24);
    #navbar-left-collapse {
        display: none;
    }
    ~ .container,
    ~ #search-bar {
        padding-left: $navbar-left-width;
    }
    @include media-breakpoint-up(lg) {
        display: block;
        ~ .container-fluid {
            padding-left: $navbar-left-width + math.div($grid-gutter-width, 2);
        }
    }
}

/* navbar-left md, sm & xs devices*/

@include media-breakpoint-down(lg) {
    #navbar-left-toggler-wrapper {
        top: 4.75rem;
        z-index: 1 !important;
        display: inline-block;
        width: 54px;
    }
    #navbar-left-wrapper {
        #navbar-left-collapse {
            display: block;
        }
        ~ .container,
        ~ #search-bar {
            padding-left: 15px;
        }
        ~ #search-bar {
            margin-left: 64px;
        }
    }

    .w3-animate-left {
        position: relative;
        animation: animateleft 0.4s;
    }
    @keyframes animateleft {
        from {
            left: -$navbar-left-width;
            opacity: 0;
        }
        to {
            left: 0;
            opacity: 1;
        }
    }
}

#navbar-left {
    z-index: 100;
    position: relative;
    width: 100%;
    color: $navbar-left-color;
    background-color: $navbar-left-background;
    box-shadow: 0 1px 0 rgba(3, 3, 3, 1);

    .h4 {
        font-weight: normal;
    }

    ul,
    li {
        margin: 0;
        padding: 0;
        width: inherit;
    }

    > li {
        /* main links*/
        > a {
            padding: 12px 20px 12px 18px;
            border-top: 1px solid lighten($navbar-left-background, 10%);
            border-bottom: 1px solid darken($navbar-left-background, 8%);
            text-shadow: 1px 1px 0 lighten($navbar-left-background, 10%);
            color: $navbar-left-color;
            background-color: lighten($navbar-left-background, 5%);
            font-size: 13px;
            font-weight: 300;

            .badge {
                text-shadow: none;
            }

            i {
                line-height: 20px;
            }
            /* active main links*/
            &:hover,
            &.active {
                background-color: darken($navbar-left-active-background, 15%);
            }
        }

        a[aria-expanded="true"],
        a[aria-expanded="false"] {
            i {
                transition: all 0.3s ease-in;
            }

            &[aria-expanded="true"] {
                i {
                    rotate: -180deg;
                }
                ~ ul {
                    border-top: 1px solid #494f4f;
                    border-bottom: 1px solid #212424;
                }
            }
        }

        /* 2nd level*/
        > ul {
            > li {
                /* all secondary levels links*/
                width: 100%;
                a {
                    font-size: 13px;
                    font-weight: 300;
                    line-height: 20px;
                    display: block;
                    padding: 5px 22px 5px 30px;
                    text-decoration: none;
                    text-shadow: none;
                    border-top: 1px solid lighten($navbar-left-background, 15%);
                    border-bottom: 1px solid darken($navbar-left-background, 5%);
                    text-shadow: 1px 1px 0 lighten($navbar-left-background, 10%);
                    color: $navbar-left-color;
                    background-color: lighten($navbar-left-background, 8%);

                    .badge {
                        text-shadow: none;
                        text-transform: uppercase;
                    }

                    i {
                        line-height: 20px;
                        font-size: 18px;
                    }

                    &:hover,
                    &.active {
                        background-color: $navbar-left-active-background;
                    }
                }

                &:first-child a {
                    border-top: none;
                }

                &:last-child a {
                    border-bottom: none;
                }

                /* 3rd level*/
                > ul {
                    li {
                        /* all secondary levels links*/
                        width: 100%;
                        list-style-type: square;
                        a {
                            font-size: 13px;
                            line-height: 20px;
                            display: block;
                            padding: 5px 22px 5px 40px;
                            text-decoration: none;
                            text-shadow: none;
                            color: transparentize($navbar-left-color, 0.25);
                            background-color: lighten($navbar-left-background, 10%);

                            i {
                                line-height: 20px;
                                font-size: 18px;
                            }

                            &:before {
                                display: inline-block;
                                content: "â– ";
                                padding-right: 7px;
                            }

                            &:hover,
                            &.active {
                                background-color: lighten($navbar-left-active-background, 10%);
                            }
                        }
                    }
                }
            }
        }
    }
}
